
import Activity from "@/components/Activity.vue";
import { Section, SectionType } from "@/model/Section";
import { store } from "@/store";
import { LISTEN_CARS } from "@/store/modules/appState";
import { defineComponent } from "vue";
import { CarService } from "@/services/CarService";

export default defineComponent({
  components: { Activity },

  mounted() {
    store.dispatch(LISTEN_CARS);
  },

  data() {
    const cars = store.state.appState.cars;
    const sections: Section[] = [
      {
        id: "odometer",
        question: this.$t.stopOdometer,
        type: SectionType.OPEN,
        mandatory: true,
        inputMode: "numeric",
      },
      {
        id: "declarations",
        question: this.$t.declarations,
        type: SectionType.OPEN,
        mandatory: false,
      },
    ];
    return { sections };
  },

  methods: {
    async onSubmit(res: any) {
      //TODO - Soluzione provvisoria
      if (store.state.appState.user?.usingCar) {
        this.$router.push("/");
      }

      const carService = new CarService();
      const carPlate = store.state.appState.user?.car;
      if (carPlate) {
        await carService.leaveCar(res.odometer, carPlate, res.declarations);
        this.$router.push("/");
      }
    },
  },
  computed: {
    carModel: () =>
      store.getters.usedCar ? store.getters.usedCar.model : null,
  },
});
